@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap');

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;

}

body {
  font-family: 'Poppins', sans-serif;
  background-color: rgb(220, 220, 220);
}

.mainArea {
  max-width: 500px;
  margin: 30px auto;
  overflow: auto;
  min-height: 300px;
  border: 2px solid black;
  padding: 30px;
  border-radius: 5px;
  box-shadow: 5px 10px darkgray;
  background-color: white;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.btn {
  display: inline-block;
  background: #000;
  color: #fff;
  border: none;
  padding: 10px 20px;
  margin: 5px;
  border-radius: 5px;
  cursor: pointer;
  text-decoration: none;
  font-size: 15px;
  font-family: inherit;
  box-shadow: 2px 4px lightgrey;
}

.btn:focus {
  outline: none;
}

.btn:active {
  transform: scale(0.98);
}

.btn-block {
  display: block;
  width: 100%;
}

.todo {
  background: lightgray;
  margin: 5px;
  padding: 10px 20px;
  cursor: pointer;
  border-radius: 10px;
}

.todo.reminder {
  border-left: 5px solid rgb(255, 252, 96);
}

.todo h3 {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.add-form {
  margin-bottom: 40px;
}

.form-control {
  margin: 20px 0;
}

.form-control label {
  display: block;
}

.form-control input {
  width: 100%;
  height: 40px;
  margin: 5px;
  padding: 3px 7px;
  font-size: 17px;
}

.form-control-check {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.form-control-check label {
  flex: 1;
}

.form-control-check input {
  flex: 2;
  height: 20px;
}

.input-box {
  border : none;
  background-color: lightgray;
  box-shadow: 2px 4px lightblue;
  border-radius: 10px;
}

footer {
  margin-top: 30px;
  text-align: center;
}
